@import url(https://maxcdn.bootstrapcdn.com/font-awesome/4.2.0/css/font-awesome.min.css);


* {
  box-sizing: border-box; }

.App {
  text-align: center;
  width: 70%;
  margin: 64px auto; }
  @media (max-width: 899px) {
    .App {
      width: 90%; } }
  .App__column {
    height: 630px; }
    @media (min-width: 900px) {
      .App__column {
        width: 50%;
        float: left; } }
    .App__column.banner {
      background: url(/weather-forecast/static/media/weather.994aa37b.gif) no-repeat center; }
      @media (max-width: 899px) {
        .App__column.banner {
          background: url(/weather-forecast/static/media/clouds.240873ff.gif) no-repeat center;
          height: 200px; } }
    .App__column.content {
      background-color: #2a2a2a;
      padding: 0 32px 16px; }
      @media (max-width: 899px) {
        .App__column.content {
          height: auto; } }
    .App__column__title {
      color: #2a2a2a;
      padding: 16px 32px;
      text-align: left;
      text-transform: uppercase; }

body {
  background-color: #ff7562;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.search {
  margin: 0 auto;
  padding: 32px 0; }
  .search form {
    /* Change autocomplete styles in WebKit */ }
    @media (max-width: 400px) {
      .search form {
        position: relative; } }
    .search form input {
      width: 30%;
      display: inline-block;
      margin-right: 16px;
      padding: 8px;
      border: none;
      border-bottom: 1px solid #ff7562;
      background-color: transparent;
      color: white;
      font-size: 16px; }
      @media (max-width: 400px) {
        .search form input {
          width: 80%;
          display: block;
          margin-bottom: 8px; } }
      .search form input:focus {
        outline: none; }
    .search form input:-webkit-autofill,
    .search form input:-webkit-autofill:hover,
    .search form input:-webkit-autofill:focus,
    .search form textarea:-webkit-autofill,
    .search form textarea:-webkit-autofill:hover,
    .search form textarea:-webkit-autofill:focus,
    .search form select:-webkit-autofill,
    .search form select:-webkit-autofill:hover,
    .search form select:-webkit-autofill:focus {
      -webkit-text-fill-color: white;
      -webkit-box-shadow: 0 0 0px 1000px transparent inset;
      -webkit-transition: background-color 5000s ease-in-out 0s;
      transition: background-color 5000s ease-in-out 0s; }
  .search .submit-search {
    padding: 0;
    background: none;
    border: none;
    font-size: 18px;
    color: #00b4b4;
    cursor: pointer; }
    @media (max-width: 400px) {
      .search .submit-search {
        position: absolute;
        right: 0;
        top: 50px; } }
    .search .submit-search:hover {
      -webkit-transform: scale(1.5);
              transform: scale(1.5); }
    .search .submit-search:focus {
      outline: none; }

.weather {
  color: white; }
  .weather__date {
    margin-bottom: 8px;
    font-weight: bold; }
  .weather__forecast {
    background: #00b4b4;
    color: white;
    margin-bottom: 0;
    padding: 10px; }
  .weather__city {
    margin: 0; }
  .weather__icon {
    padding: 8px; }
  .weather .BrainhubCarousel {
    align-items: inherit; }
    .weather .BrainhubCarousel__arrows {
      background-color: transparent; }
      .weather .BrainhubCarousel__arrows:hover {
        background-color: transparent;
        -webkit-transform: scale(1.5);
                transform: scale(1.5); }
  .weather .fa-caret-down {
    color: #00b4b4;
    font-size: 50px;
    margin-top: -20px;
    margin-bottom: 8px; }
  .weather p {
    margin-top: 0;
    font-size: 16px; }
  .weather__temp {
    margin-bottom: 8px;
    font-weight: bold; }
  .weather__desc {
    margin-bottom: 8px; }
  .weather .bold_data {
    font-weight: bold;
    font-size: 20px; }
  .weather .icon {
    font-size: 32px;
    color: #ffff7c;
    margin: 16px 0; }

.lds-ripple {
  display: inline-block;
  position: relative;
  width: 64px;
  height: 64px;
  margin-top: 180px; }
  @media (max-width: 899px) {
    .lds-ripple {
      margin: 16px 0; } }

.lds-ripple div {
  position: absolute;
  border: 4px solid #fff;
  opacity: 1;
  border-radius: 50%;
  -webkit-animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite;
          animation: lds-ripple 1s cubic-bezier(0, 0.2, 0.8, 1) infinite; }

.lds-ripple div:nth-child(2) {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s; }

@-webkit-keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0; } }

@keyframes lds-ripple {
  0% {
    top: 28px;
    left: 28px;
    width: 0;
    height: 0;
    opacity: 1; }
  100% {
    top: -1px;
    left: -1px;
    width: 58px;
    height: 58px;
    opacity: 0; } }

.footer {
  font-weight: 100;
  font-size: 14px;
  padding: 16px;
  clear: both; }
  .footer a {
    color: #2a2a2a;
    text-decoration: none; }
    .footer a:hover, .footer a:focus, .footer a:active {
      font-weight: 400;
      text-decoration: underline; }

